export const showErrorsOnly = ({
  csvData,
  csvField,
  isShowErrorOnly,
  setIsShowErrorOnly,
  setIsHiddenRows,
  setCheckingCsv,
}) => {
  // ;
  let tmp_arr_false = [];
  let tmp_arr_true = [];
  csvData.map((data, index) => {
    let stat = true;
    let i = index + 1;
    csvField.map((dataColumn, indexColumn) => {
      let key = dataColumn.dataField + "Status";
      if (data[key] === false) {
        stat = false;
      }
    });

    if (stat === false) {
      if (!tmp_arr_false.includes(i)) {
        tmp_arr_false.push(i);
      }
    } else {
      if (!tmp_arr_true.includes(i)) {
        tmp_arr_true.push(i);
      }
    }
  });

  //return array
  let ret = [];
  if (isShowErrorOnly === true) {
    //true = delete all row
    ret = tmp_arr_true.filter((x) => !tmp_arr_false.includes(x));
    if (ret.length == csvData.length) {
      return 0;
    }
  } else {
    ret = [];
  }
  setIsHiddenRows(ret);
  setIsShowErrorOnly(!isShowErrorOnly); //untuk button show all atau error only
  setCheckingCsv(true);
};

export const deleteErrorsField = ({
  csvData,
  csvField,
  dispatch,
  setCheckingCsv,
}) => {
  let new_arr = [];
  let tmp = null;
  csvData.map((data, index) => {
    let stat = true;
    csvField.map((dataColumn, indexColumn) => {
      let key = dataColumn.dataField + "Status";
      let i = index + 1;
      if (data[key] === false) {
        stat = false;
      }
    });
    if (stat === true) {
      new_arr.push(data);
    }
  });

  dispatch({ type: STOK_TAKE_UPLOAD_JSON, csvData: new_arr });
  setCheckingCsv(true);
};

export const setCsvData = ({ param, dispatch, setCheckingCsv }) => {
  dispatch({ type: STOK_TAKE_UPLOAD_JSON, csvData: param });
  setCheckingCsv(true);
};

export const checking_error = ({
  csvData,
  csvField,
  setIsValidate,
  setCheckingCsv,
}) => {
  if (csvData === undefined) {
    return 0;
  }
  let stats = true;
  csvData.map((data, index) => {
    csvField.map((dataField, indexField) => {
      let status = data[dataField.dataField + "Status"];
      if (status === false) {
        stats = false;
      }
    });
  });
  if (stats === false) {
    setIsValidate(false);
  } else {
    setIsValidate(true);
  }
  setCheckingCsv(false);
};

const setHeader = (csvData) => {
  //get time - created at
  let time = new Date().getTime();
  let d = new Date(time);
  let n = d.getMilliseconds();
  let list_area = [];
  let return_data = [];
  let warehouse_source = csvData[0].warehouseSource || "";

  csvData.map((data, index) => {
    //in outbound csv upload only check area source
    if (!list_area.includes(data.areaSource)) {
      return_data.push({
        id: index,
        warehouseSource: warehouse_source,
        warehouseDestination: warehouse_source,
        areaSource: data.areaSource,
        areaDestination: data.areaSource,
        list: [],
        createdAt: n,
      });
      list_area.push(data.areaSource);
    }
  });
  return return_data;
};

const getSKU = (csvData) => {
  let return_data = []; 
  let today = new Date();
  let dd = String(today.getDate()).padStart(2, '0');
  let mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
  let yyyy = today.getFullYear();
  today = dd + '-' + mm + '-' + yyyy;

  csvData.map((data, index) => {
    let available = false;
    return_data.push(
    {
      isSuccess: true,
      skuUuid: data.skuUuid,
      warehouseId: data.warehouseDestinationUuid,
      unitOfMeassures: data.uom,
      qty: data.qty || 0,
      photo: data.photo,
      poSku: data.wmsPoSkuCode,
      expiryDate: moment(data.expiryDateCol, "DD-MM-YYYY").toDate().getTime() || "",
      inboundDate: moment(today, "DD-MM-YYYY").toDate().getTime() || "",
      errorMessage: "",
      category: data.category,
      skuNumber: data.skuNumber,
      skuDescription: data.skuDescription,
      warehouseCode: data.warehouseDestination
    } 
    );
  });
  return return_data;
};
 
 

const setResult = (result) => { 
  let i = 1;
  
  let arrayResult = {
    source : "upload",
    codeTransaction : "inbound",
    list: []
  }
  let tmp_arr = []

  result.data.map((data, indexHeader) => { 
    let isStatus = "Failed";
    if (data.isSuccess) {
      isStatus = "Success";
    }
    
    tmp_arr.push({ 
      wmsPoSkuCode:data.poSku,
      qty:data.qty,
      expiryDateCol: moment(data.expiryDate).format("DD-MM-YYYY"),   
      uploadStatus: isStatus || "", 
      uploadStatusReason: data.errorMessage || "",
      category: data.category,
      skuNumber: data.skuNumber,
      skuDescription: data.skuDescription,
      warehouseDestination: data.warehouseCode
    })
  }); 
  return tmp_arr;
};

export const submit = async ({
  csvData,
  csvField,
  dispatch,
  selectorStatus,
  setIsShowModal,
  setIsSubmitText,
  setSubmitStatus,
}) => {
  setIsSubmitText("Loading...");
  console.clear();

  //set header
  let header = {
    source : "upload",
    codeTransaction : "inbound",
    list: []
  }

  //get list sku
  let skuList = await getSKU(csvData);

  header.list = skuList; 

  //submit
  let execute = await submitCsvInbound({
    dataCsv: header,
    setSubmitStatus,
    dispatch
  });
  if (execute == "error") {
    return 0;
  }

  // set result
  let result = await setResult(execute);
  console.log(result);
  await dispatch({ type: STOK_TAKE_UPLOAD_JSON, csvDataResult: result });

  //renew schema for result
  let newSchema = [];
  newSchema.push({
    dataField: "uploadStatus",
    text: "Upload Status",
    type: "string",
    sort: true,
  });
  csvField.map((data, index) => {
    newSchema.push(data);
  });
  await dispatch({ type: STOK_TAKE_UPLOAD_JSON, csvFieldResult: newSchema });

  setIsShowModal(false);
  setIsSubmitText("Submit");
  setSubmitStatus(true);
};


export const validasiDouble = ({
  selectorCsvData,
  setNewCsvData,
  setRowDouble,
  dispatch,
}) => {
  let skuNumberArray = [];
  let skuData = {};
  let csvDataValidated = [];
  let rowDouble = [];
  const y = selectorCsvData?.map((data, idx) => {
    if (!skuNumberArray.includes(data.skuNumber)) {
      skuNumberArray.push(data.skuNumber);
    }

    let objName = "sku_" + data.skuNumber;
    if (!skuData[objName]) {
      let tmp = {
        warehouseDestination: data.warehouseDestination, 
        skuNumber: data.skuNumber
      };
      skuData[objName] = [tmp];
    } else {
      let dataArraySku = skuData[objName];
      let pushData = true;
      dataArraySku.map((d, i) => {
        if ( 
          d.skuNumber == data.skuNumber && 
          d.warehouseDestination == data.warehouseDestination
        ) {
          //data sama, tidak di push
          pushData = false;
          rowDouble.push(data.id);

          //set to red
          data.warehouseDestinationStatus = false; 
          data.skuNumberStatus = false;
          data.wmsPoSkuCodeStatus = false;
          data.skuDescriptionStatus = false;
          data.categoryStatus = false;
          data.expiryDateColStatus = false;
          data.qtyStatus = false;
          data.warehouseDestinationReason = "Data Duplicated";  
          data.skuNumberReason = "Data Duplicated";
          data.wmsPoSkuCodeReason = "Data Duplicated";
          data.skuDescriptionReason = "Data Duplicated";
          data.categoryReason = "Data Duplicated";
          data.expiryDateColReason = "Data Duplicated";
          data.qtyReason = "Data Duplicated";
        }
      });
      if (pushData) {
        let tmp = {
          warehouseDestination: data.warehouseDestination, 
          skuNumber: data.skuNumber, 
        };
        skuData[objName].push(tmp);
      }
    }

    console.log("data", data);
    csvDataValidated.push(data);
  });

  console.log(csvDataValidated);
  // setRowDouble([3]);
  // dispatch({ type: STOK_TAKE_UPLOAD_JSON, csvData: csvDataValidated });
  // return { data: csvDataValidated, doubleRow: rowDouble };
  setNewCsvData(csvDataValidated);
  setRowDouble(rowDouble);
};